body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f4f4f4;
}

.app {
  text-align: center;
}

nav {
  background-color: #333;
  padding: 1rem;
}

nav ul {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: center;
}

nav ul li {
  margin: 0 15px;
}

nav ul li a {
  color: white;
  text-decoration: none;
  font-size: 18px;
}

nav ul li a:hover {
  text-decoration: underline;
}

.home, .upload, .image-list {
  padding: 20px;
}

.upload input {
  margin-top: 10px;
}

.image-preview img {
  width: 300px;
  margin-top: 20px;
  border: 2px solid #ddd;
}

h1, h2 {
  color: #333;
}

p {
  color: #666;
}
